import React, { Component } from 'react';
import { Modal, Button,  Form, Icon, Input, Checkbox,Table, Divider, Tag,Tabs } from 'antd';
const { Column, ColumnGroup } = Table;
const FormItem = Form.Item;
const TabPane = Tabs.TabPane;



class User extends Component {
  constructor(props){
    super(props);
      this.state = {
        getUserData:[],
        getBlockedUser:[],
        addUserVisible:false,
        addUserEmail:null,
        addUserFirstname:null,
        addUserMiddlename:null,
        addUserLastname:null,
    }
  }

  /*
             |||||||
            ( O   O )
     ____oOO___(_)___OOo____
    (_______________________) Constructor
  */

  //CRUD
  getUser = () => {
    const data = JSON.stringify({
    "query":"query( $limit:limit,$sort:sort,$search:search ){ GetUser( limit:$limit,sort:$sort,search:$search ) {sortableFields,searchableFields,page { items,pageNo,totalPages },data{id,email,firstname,lastname,middlename,created },success,message { path,message } } }",
      "variables":{
        "limit":{
          "to":6,
          "page":0
        },
        "sort":{
          "field":"",
          "by":""
        },
        "search":{
          "by":"",
          "text":""
        }
      }
    })
   fetch("https://api.8uilder.com",{
      method:'POST',
      headers: {
        'Content-Type': 'application/json',
        "Authorization":window.localStorage.getItem("session")
      },
      body:data
    }).then(async res =>{
      const response = await res.json()
      let Result = response.GetUser.data;
      for (let item in Result){
        Result[item].key=item;
        // Result[item].id = item;
      }
      if(response.GetUser.success){
        this.setState({
          getUserData:Result
        })
        // console.log("getUserData: ", this.state.getUserData)
      }else{
        return false
      }
    })
  }
  addUser = async () => {
    const data = JSON.stringify({
      "query":"mutation($input:create_user!){ CreateUser( input:$input ) { success,message { path,message } } }",
        "variables":{
          "input":{
            "email":this.state.addUserEmail,
            "firstname":this.state.addUserFirstname,
            "middlename":this.state.addUserLastname,
            "lastname":this.state.addUserMiddlename
        }
      }
    })
    return await fetch("https://api.8uilder.com",{
      method:'POST',
      headers: {
        'Content-Type': 'application/json',
        "Authorization":window.localStorage.getItem("session")
      },
      body:data
    }).then(async res =>{
      const response = await res.json()
      console.log("addUser: ",response)
      if(response.CreateUser.success){
        return true
      }else{
        return false
      }
    })
  }

  blockUser = async (e) => {
    let blockerUserId = e
    const data = JSON.stringify({
      "query":"mutation($input:String!){ blockUser( input:$input ) { success,message { path,message } } }",
        "variables":{
            "input":blockerUserId
      }
    })
    return await fetch("https://api.8uilder.com",{
      method:'POST',
      headers: {
        'Content-Type': 'application/json',
        "Authorization":window.localStorage.getItem("session")
      },
      body:data
    }).then(async res =>{
      const response = await res.json()
      console.log("deleteUser: ",response)
      if(response.blockUser.success){
        return true
      }else{
        return false
      }
    })
  }

  unblockUser = async (e) => {
    let blockerUserId = e
    const data = JSON.stringify({
      "query":"mutation($input:String!){ unblockUser( input:$input ) { success,message { path,message } } }",
      "variables":{
        "input":blockerUserId
      }
    })
    return await fetch("https://api.8uilder.com",{
      method:'POST',
      headers: {
        'Content-Type': 'application/json',
        "Authorization":window.localStorage.getItem("session")
      },
      body:data
    }).then(async res =>{
      const response = await res.json()
      console.log("deleteUser: ",response)
      if(response.unblockUser.success){
        return true
      }else{
        return false
      }
    })
  }

  //SUCCESS
  success = () => {
  Modal.success({
    title: 'Success',
    maskClosable:'false'
    });
  }


  //OPEN MODAL
  openAddUserModal = () =>{
    this.setState({
      addUserVisible:true
    })
  }

  //CLOSE MODAL
  closeAddUserModal = () =>{
    this.setState({
      addUserVisible:false
    })
  }
  //MODAL
  addUserModal = async () => {
    if(await this.addUser()){
      this.success()
      let addUserInput = await document.getElementsByClassName('addUserInput')
      for(var i = 0;i < addUserInput.length;i++){
        addUserInput[i].children[1].value = ""
      }
      console.log(addUserInput)
      this.setState({
        addUserVisible: false,
      });

      this.getUser()
    }else{
      Modal.error({
        title: 'This is an error message',
        maskClosable:'false'
      });
    }
  }

  blockUserModal = async (e) => {
    let records = e
    if(await this.blockUser(records.id)){
      this.success()
      this.getUser()
    }else{
      Modal.error({
        title: 'This is an error message',
        maskClosable:'false'
      });
    }
  }

  unblockUserModal = async (e) => {
    let records = e
    if(await this.unblockUser(records.id)){
      this.success()
      this.getBlockedUser()
    }else{
      Modal.error({
        title: 'This is an error message',
        maskClosable:'false'
      });
    }
  }


  /*
    __-----_________________{]__________________________________________________
   {&&&&&&&#%%&#%&%&%&%&%#%&|]__________________________________________________\  Block User
                            {]
  */

  getBlockedUser = () => {
    const data = JSON.stringify({
      "query":"query( $limit:limit,$sort:sort,$search:search ){ GetBlockUser( limit:$limit,sort:$sort,search:$search ) {sortableFields,searchableFields,page { items,pageNo,totalPages },data{id,email,firstname,lastname,middlename,created },success,message { path,message } } }",
       "variables":{
           "limit":{
               "to":100,
               "page":1
           },
               "sort":{
               "field":"",
               "by":""
           },
       "search":{
             "by":"",
             "text":""
         }
       }
    })
   fetch("https://api.8uilder.com",{
      method:'POST',
      headers: {
        'Content-Type': 'application/json',
        "Authorization":window.localStorage.getItem("session")
      },
      body:data
    }).then(async res =>{
      const response = await res.json()
      let Result = response.GetBlockUser.data;
      for (let item in Result){
        Result[item].key=item;
        // Result[item].id = item;
      }
      if(response.GetBlockUser.success){
        this.setState({
          getBlockedUser:Result
        })
        console.log("getBlockedUser: ", this.state.getBlockedUser)
      }else{
        return false
      }
    })
  }



  //ONCHANGE
  onChange = (e) =>{
    this.setState({[e.target.name]:e.target.value})
  }
  //TAB PANE
  callback = (key) => {
    console.log(key)
    if(key == 1){
      this.getUser()
    }else{
      this.getBlockedUser()
    }
  }

  checkRecord = (e) => {
    // console.log(e)
  }

  /*
             |||||||
            ( O   O )
     ____oOO___(_)___OOo____
    (_______________________) Methods
  */

  componentWillMount(){
    this.getUser()
    this.getBlockedUser()
  }

  /*
             |||||||
            ( O   O )
     ____oOO___(_)___OOo____
    (_______________________) Life Cycle
  */

  render() {

    return (
      <div className="user">
      
        <Tabs defaultActiveKey="1" onChange={(e) => this.callback(e)}>
          <TabPane tab="Users" key="1">
            <div className="Table">
              <h1>Users</h1>
              <div className="row" style={{marginBottom:"10px"}}>
                <div className="col-sm-3"></div>
                <div className="col-sm-3"></div>
                <div className="col-sm-3"></div>
                <div className="col-sm-3">
                  <button className="btn btn-sm btn-primary" style={{float:"right"}} onClick={this.openAddUserModal}>Add User</button>
                </div>
              </div>
              <Table dataSource={this.state.getUserData}>
                <Column title="Date Created"dataIndex="created"/>
                <Column title="Email"dataIndex="email"/>
                <Column title="Firstname" dataIndex="firstname" />
                <Column title="Lastname" dataIndex="lastname"/>
                <Column title="Middlename" dataIndex="middlename" onCell={(e) => this.checkRecord(e)}/>
                <Column title="Action" key="action"
                  render={(text, record) => (
                    <span>
                      <a href="#" onClick={() => this.blockUserModal(record)}>Edit</a>
                      <Divider type="vertical" />
                      <a href="#" onClick={() => this.blockUserModal(record)}>Block</a>
                      <Divider type="vertical" />
                      <a href="#"  onClick={() => this.blockUserModal(record)}>View</a>
                    </span>
                   )}
                />
              </Table>
            </div>
            <div className="Modal">
              <Modal
                title="Add Users"
                visible={this.state.addUserVisible}
                onOk={this.addUserModal}
                onCancel={this.closeAddUserModal}
                maskClosable={false}>
                <div>
                  <Form className="login-form">
                    <FormItem>
                      <Input prefix={<Icon type="key" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="Email" className="addUserInput"  onChange={this.onChange} name="addUserEmail" />
                    </FormItem>
                    <FormItem>
                      <Input prefix={<Icon type="key" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="Firstname" className="addUserInput" onChange={this.onChange} name="addUserFirstname" />
                    </FormItem>
                    <FormItem>
                      <Input prefix={<Icon type="key" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="Middlename" className="addUserInput" onChange={this.onChange} name="addUserMiddlename" />
                    </FormItem>
                    <FormItem>
                      <Input prefix={<Icon type="key" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="Lastname" className="addUserInput" onChange={this.onChange} name="addUserLastname" />
                    </FormItem>
                 </Form>
                </div>
              </Modal>
            </div>
          </TabPane>
          <TabPane tab="Block Users" key="2">
            <div className="Table">
              <h1>Blocked Users</h1>
              <div className="row" style={{marginBottom:"10px"}}>
                <div className="col-sm-3"></div>
                <div className="col-sm-3"></div>
                <div className="col-sm-3"></div>
                <div className="col-sm-3">
                </div>
              </div>
              <Table dataSource={this.state.getBlockedUser}>
                <Column title="Date Created"dataIndex="created"/>
                <Column title="Email"dataIndex="email"/>
                <Column title="Firstname" dataIndex="firstname"/>
                <Column title="Lastname" dataIndex="lastname"/>
                <Column title="Middlename" dataIndex="middlename"/>
                <Column title="Action" key="action"
                  render={(text, record) => (
                    <span>
                      <a href="#" onClick={() => this.unblockUserModal(record)}>Unblock</a>
                    </span>
                   )}
                />
              </Table>
            </div>
          </TabPane>
        </Tabs>
      </div>
    );
  }
}

export default User;
